.jumbotron
{
    background-image: url('https://cdn.s3waas.gov.in/s3b534ba68236ba543ae44b22bd110a1d6/uploads/2018/03/2018033167.jpg');
    background-size: cover;
    color: white;
    width: 100%;
    height: 100%;
    filter: grayscale(100%);
    z-index: 2;
}

.jumbotron:hover
{
    filter: none;
    color: white;
    background: rgba(0,0,0,1);
    transition: all 1s;
}

