.about-section
{
    text-align: start;
    vertical-align: middle;
    padding: 2rem;
    display: inline-block;
}
.about-section img
{
    width: 20rem;
    height: 18rem;
}
.about-text
{
    display: inline-block;
    width: 50rem;
    text-align: start;
    vertical-align: middle;
    margin-left: 2rem;
}

.about-name
{
    font-size: 2.5rem;
    font-family: cursive;
}
.about-para
{
    font-size: 1.1rem;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

@media only screen and (max-width: 600px)
{
    .about-name
    {
        font-size: 1.8rem;
        font-family: cursive;
        text-align: center;
    }
    .about-text
    {
        width: 90%;
        vertical-align: middle;
        text-align: start;
        margin-left: 0;
    }
    .about-para
    {
        font-size: 1rem;
        font-family: Georgia, 'Times New Roman', Times, serif;
    }
}